body {
  text-align: start; /* HACK fallback */
  text-align: initial; /* fix unsupported browsers initial value */
  background: #fff;
  color: #000;
}

sup, sub {
  vertical-align: initial;  /* HACK fallback */
  vertical-align: unset;
}
body,
h1, h2, h3, h4, h5, h6,
hr,
p, figure, blockquote, pre,
ol, ul, dl,
dd,
fieldset {
  margin: initial; /* HACK fallback */
  margin: unset;
}
ol, ul,
th, td,
legend {
  padding: initial; /* HACK fallback */
  padding: unset;
}
h1, h2, h3, h4, h5, h6,
address,
pre,
th,
em, strong, i, small,
dfn, b, var, cite, sup, sub,
code, kbd, samp {
  font: inherit;
}
caption,
th {
  text-align: inherit; /* HACK fallback */
  text-align: unset;
}
pre {
  white-space: inherit; /* HACK fallback */
  white-space: unset;
}
a,
u, s,
ins, del {
  text-decoration: initial; /* HACK fallback */
  text-decoration: unset;
}
mark {
  color: inherit; /* HACK fallback */
  color: unset;
}

